import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import ReactSelect from 'react-select';
import 'react-select/dist/react-select.css';

const options = [
    {id: 395, category: "Megatron"},
    {id: 396, category: "Star Scream"},
    {id: 397, category: "Bumble Bee"},
    {id: 398, category: "Opptimus Prime"},
    {id: 399, category: "Rodimus"},
    {id: 400, category: "Quintessa"},
]

class App extends Component {
  state = {
      value: '',
      selectedId: options[0]['id'],
      selectOpen: false,
      options
  };

  onChange=(value)=>{
      this.setState({value})
  };
  handleListToggle=()=>{
      this.setState({selectOpen: !this.state.selectOpen})
  };
  handleChoiceSelect=(id)=>{
      this.setState({selectedId: id, selectOpen: false});
  };
  renderTestSelectOptions(){
      const {selectedId} = this.state;
      return options.map((obj, key) => {
          const selected = `${obj.id === selectedId}`;
          return <li key={key} id={obj.id} role="option" 	aria-selected={selected} onClick={() => this.handleChoiceSelect(obj.id)}>
              {obj.category}
          </li>
      })
  }
  renderTestSelect(){
      const {options, selectedId, selectOpen} = this.state;
      const hiddenStyle = {
          border: 0,
          clip: 'rect(0 0 0 0)',
          height: 1,
          margin: -1,
          overflow: 'hidden',
          padding: 0,
          position: 'absolute',
          width: 1,
      };
      //took out display: table to test the difference
      const wrapperStyle = {
          backgroundColor: '#fff',
          borderColor: '#d9d9d9 #ccc #b3b3b3',
          borderRadius: 4,
          border: '1px solid #ccc',
          color: '#333',
          cursor: 'default',
          borderSpacing: 0,
          borderCollapse: 'separate',
          height: 36,
          outline: 'none',
          overflow: 'hidden',
          position: 'relative',
          width: '100%',
      };

      return (
          <div>
              <span id="exp_elem">
                Choose a Transformer:
              </span>
              <div style={wrapperStyle}>
                  <button
                      aria-haspopup="listbox"
                      id="exp_button"
                      aria-labelledby="exp_elem exp_button"
                      onClick={this.handleListToggle}
                      style={{border: 0, width: '100%', height: '100%'}}
                  >
                      {options.find(obj => obj.id === selectedId).category}
                  </button>
              </div>
              <ul id="exp_elem_list"
                  tabIndex="-1"
                  role="listbox"
                  aria-labelledby="exp_elem"
                  style={!selectOpen ? hiddenStyle : {}}>
                  {this.renderTestSelectOptions()}
              </ul>
          </div>
      )
  }
  render() {

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
          <ReactSelect
            aria-labelledby="email-compose-subject-select-id"
            options={options}
            placeholder="Select ME"
            valueKey='id'
            labelKey='category'
            value={this.state.value}
            onChange={this.onChange}
          />
          {this.renderTestSelect()}
      </div>
    );
  }
}

export default App;



